// $phone: "(min-width: 480px) and (max-width: 768px)"
// $tablet: "(min-width: 768px) and (max-width: 1168px)"
main {
	font-weight: bold;
	background: linear-gradient( rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5) ), url('../images/home/banner.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	height: 620px;
	.navbar {
		background: transparent;
		border-bottom: 0px;
		.container {
			img {
				display: inline-block;
				padding: 5px; }
			ul {
				display: inline-block;
				font-size: 1.3em;
				padding: 10px;
				li {
					margin: 0 15px; } } } }
	.container .upper-row {
		text-align: justify;
		font-family: 'Amatic SC', cursive;

		h2, p {
			text-align: center;
			color: #fff;
			font-size: 50px; }
		h2 {
			font-weight: bolder;
			text-shadow: 1px 1px #220022;
			font-size: 100px; }
		.explain {
			font-family: 'Shadows Into Light', cursive;
			font-size: 37px; }
		p {
			float: right;
			a {
				display: block;
				button {
					font-size: 50px;
					border-radius: 7px;
					margin-top: 10px;
					padding: 5px 25px; } } } } }

@media screen and (min-width: 300px) and (max-width: 625px) {
	main {
		height: 480px;
		.navbar {
			background: transparent;
			border-bottom: 0px;
			margin-bottom: 0px;
			.container {
				img {
					display: inline-block;
					height: 35px; }
				ul {
					display: inline-block;
					font-size: 1.1em;
					padding: 5px;
					li {
						margin: 0 10px; } } } }
		.container .upper-row {
			h2,p {
				font-size: 30px;
				font-weight: bold; }
			h2 {
				font-size: 40px; }
			.explain {
				font-size: 25px; }
			p {
				a {
					button {
						font-size: 30px;
						margin-top: 5px; } } } } } }
@media screen and (min-width: 626px) and (max-width: 768px) {
	main {
		height: 530px;
		.navbar {
			background: transparent;
			border-bottom: 0px;
			.container {
				img {
					display: inline-block; }
				ul {
					display: inline-block;
					font-size: 1.2em;
					padding: 8px;
					li {
						margin: 0 10px; } } } }
		.container .upper-row {
			h2, p {
				font-size: 42px; }
			h2 {
				font-size: 55px; }
			.explain {
				font-size: 32px;
				p {
					a {
						button {
							font-size: 40px; } } } } } } }
@media screen and (min-width: 768px) and (max-width: 1185px) {
	main {
		height: 570px;
		.navbar {
			background: transparent;
			border-bottom: 0px;
			.container {
				img {
					display: inline-block; }
				ul {
					display: inline-block;
					font-size: 1.3em;
					padding: 10px;
					li {
						margin: 0 15px; } } } }
		.container .upper-row {
			h2, p {
				font-size: 45px; }
			h2 {
				font-size: 70px; }
			.explain {
				font-size: 35px;
				p {
					a {
						button {
							font-size: 45px; } } } } } } }
